/*
 * I18n: 
 * */
import { Base } from './Base';
class I18n {
	/**
	 * [zh-cn:en-us:] zh-cn
	 * @param settings
	 * @returns {string|string}
     */
	getLanguage(settings) {
		return settings.i18n;
	}

	/**
	 * [  ]
	 * @param settings
	 * @param key 
	 * @param language : ,  settings.i18n
	 * @returns {*|string}
     */
	getText(settings, key, language) {
		return settings.textConfig[key][language || this.getLanguage(settings)] || '';
	}

	/**
	 * 
	 * @param settings
	 * @param key 
	 * @param v1 3
	 * @param v2 3
	 * @param v3 3
     * @returns {string}
     */
	i18nText(settings, key, v1, v2, v3) {
		const _this = this;
		let intrusion = [];

		// 
		if (arguments.length === 3 && typeof (arguments[2]) === 'object') {
			intrusion = arguments[2];
		} else if (arguments.length > 1) {
			for (let i = 1; i < arguments.length; i++) {
				intrusion.push(arguments[i]);
			}
		}
		let _text = '';
		try {
			_text = _this.getText(settings, key);
			if (!intrusion || intrusion.length === 0) {
				return _text;
			}
			_text = _text.replace(/{\d+}/g, word => {
				return intrusion[word.match(/\d+/)];
			});
			return _text;
		} catch (e) {
			Base.outLog(`${key}${_this.getLanguage(settings)}`, 'warn');
			return '';
		}
	}
}
export default new I18n();



