/**
 * Settings: 
 */
import { $ } from './Base';
class Settings {
	constructor() {
		/**
		 * 
		 */
		const drag = {
			// 
			supportDrag: true,

			// 
			dragBefore: $.noop,

			// 
			dragAfter: $.noop
		};

		/**
		 * 
		 */
		const adjust = {
			// 
			supportAdjust: true,

			// 
			adjustBefore: $.noop,

			// 
			adjustAfter: $.noop
		};

		/**
		 * 
		 */
		const remind = {
			// [THremind]
			supportRemind: false
		};

		/**
		 * 
		 */
		const config = {
			// []
			supportConfig: true
		};

		/**
		 * 
		 */
		const gridStyle = {
			// 
			width: '100%',

			// , 300px
			height: '300px',

			// 
			// textAlign: '',       // v2.3.15

			// 
			animateTime: 300
		};

		/**
		 * 
		 */
		const cache = {
			// 
			disableCache: false
		};

		/**
		 * 
		 */
		const sort = {
			// 
			supportSorting: false,

			// [
			isCombSorting: false,

			// , : ='date', sortKey='sort_', sort_date
			sortKey: 'sort_',

			// []
			sortData: {},

			// []
			sortUpText: 'ASC',

			// []
			sortDownText: 'DESC',

			// 
			sortingBefore: $.noop,

			// 
			sortingAfter: $.noop
		};

		/**
		 * 
		 */
		const ajaxPage = {
			// ajxa
			supportAjaxPage: false,

			// 
			sizeData: [10, 20, 30, 50, 100],

			// 
			pageSize: 20,

			// []
			pageData: {},

			// 
			query: {},

			// 
			pagingBefore: $.noop,

			// 
			pagingAfter: $.noop
		};

		/**
		 * 
		 */
		const autoOrder = {
			// 
			supportAutoOrder: true
		};

		/**
		 * 
		 */
		const checkbox = {
			// 
			supportCheckbox: true
		};

		/**
		 * 
		 */
		const i18n = {
			// [zh-cn:en-us:] zh-cn
			i18n: 'zh-cn'
		};

		/**
		 * 
		 */
		const gridData = {
			// 
			/* columnData
			columnData: [{
			 // 
			 key: 'url',

			 // 
			 text: 'url',

			 // @2.4.0
			 // ,  true
			 isShow: true,

			 // , 
			 // :
			 // 1.th GridManager
			 // 2.
			 width: '100px',

			 // 
			 // : 'left', 'center', 'right'
			 align: '',

			 // 
			 // supportSorting=true:
			 // 1'': 
			 // 2'DESC': [sortDownText]
			 // 3'ASC': [sortUpText]
			 sorting: 'DESC',

			 // ,
			 // supportRemind=true
			 remind: '',

			 // 
			 // 
			 // nodeData: 
			 // rowData: , : nodeData=== rowData.url
			 template: function(nodeData, rowData){
			 return '<a href="'+nodeData+'">'+rowData.url+'</a>';
			 }
			 }]
			*/
			columnData: [],

			// grid-manager[html]
			gridManagerName: '',

			// 
			ajax_url: '',

			// ajax['GET', 'POST']GET
			ajax_type: 'GET',

			// ajax
			ajax_headers: {},

			// @v2.4.0
			// XHR, ajax_xhrFields XHR
			//  -> ajax_xhrFields: {withCredentials: true}, cookies, authorization headers()
			ajax_xhrFields: {},

			// ajax,jToolbeforeSend
			ajax_beforeSend: $.noop,

			// ajax,jToolsuccess
			ajax_success: $.noop,

			// ajax,jToolcomplete
			ajax_complete: $.noop,

			// ajax,jToolerror
			ajax_error: $.noop,

			// ajax,ajax_url
			ajax_data: undefined,

			// ,  @v2.3.14
			requestHandler: $.noop,

			// , .  @v2.3.14
			responseHandler: $.noop,

			// ajaxkey,data
			dataKey: 'data',

			// ajaxkey,totals
			totalsKey: 'totals'
		};

		/**
		 * 
		 */
		const gridExport = {
			// 
			supportExport: true
		};

		const settings = {
			...drag,
			...adjust,
			...remind,
			...config,
			...gridStyle,
			...cache,
			...sort,
			...ajaxPage,
			...autoOrder,
			...checkbox,
			...i18n,
			...gridData,
			...gridExport
		};
		$.extend(true, this, settings);
	}
}

// 
class TextSettings {
	constructor() {
		this['order-text'] = {
			'zh-cn': '',
			'en-us': 'order'
		};
		this['first-page'] = {
			'zh-cn': '',
			'en-us': 'first'
		};
		this['previous-page'] = {
			'zh-cn': '',
			'en-us': 'previous'
		};
		this['next-page'] = {
			'zh-cn': '',
			'en-us': 'next'
		};
		this['last-page'] = {
			'zh-cn': '',
			'en-us': 'last'
		};
		this['dataTablesInfo'] = {
			'zh-cn': ' {0}-{1} {2}',
			'en-us': 'this page show {0}-{1} count {2}'
		};
		this['goto-first-text'] = {
			'zh-cn': '',
			'en-us': 'goto'
		};
		this['goto-last-text'] = {
			'zh-cn': '',
			'en-us': 'page'
		};
		this['refresh'] = {
			'zh-cn': '',
			'en-us': 'Refresh'
		};
		this['save-as-excel'] = {
			'zh-cn': 'Excel',
			'en-us': 'Save as Excel'
		};
		this['save-as-excel-for-checked'] = {
			'zh-cn': 'Excel',
			'en-us': 'Save selected as Excel'
		};
		this['config-grid'] = {
			'zh-cn': '',
			'en-us': 'Setting Grid'
		};
		this['checkall-text'] = {
			'zh-cn': '',
			'en-us': 'All'
		};
	}
}
export { Settings, TextSettings };



