import '../css/index.scss';
import { jTool, Base } from './Base';
import Adjust from './Adjust';
import Cache from './Cache';
import Config from './Config';
import Core from './Core';
import Drag from './Drag';
import Export from './Export';
import Menu from './Menu';
import Remind from './Remind';
import Scroll from './Scroll';
import Sort from './Sort';
import store from './Store';
import Hover from './Hover';
export default class GridManager {
	/**
	 * @
	 * 
	 * GridManager.version || GM.version
	 * @returns {string}
	 */
	static
	get version() {
		return store.version;
	}

	/**
	 * @
	 * Table  GridManager
	 * @param table
	 * @returns {*}
	 */
	static
	get(table) {
		// return Cache.__getGridManager(jTool(table));
		return Cache.getSettings(jTool(table));
	}

	/**
	 * @
	 * 
	 * ,
	 * @param table
	 * @returns {{}}
     */
	static
	getLocalStorage(table) {
		return Cache.getUserMemory(jTool(table));
	}

	/**
	 * @
	 * ,  table, 
	 * @param table
	 * @returns {boolean}
     */
	static
	clear(table) {
		return Cache.cleanTableCache(jTool(table), 'clear()');
	}

	/**
	 * @
	 * 
	 * @param table
	 * @param target tr[Element or NodeList]
	 * @returns {{}}
     */
	static
	getRowData(table, target) {
		return Cache.__getRowData(jTool(table), target);
	}

	/**
	 * @
	 * 
	 * @param table
	 * @param sortJson json :{th-name:'down'} valuesortUpText  sortDownText
	 * @param callback [function]
     * @param refresh [boolean, true]
     */
	static
	setSort(table, sortJson, callback, refresh) {
		Sort.__setSort(jTool(table), sortJson, callback, refresh);
	}

	/**
	 * @
	 * ThTD
	 * @param table
	 * @param target
     */
	static
	showTh(table, target) {
		Base.setAreVisible(jTool(target), true);
	}

	/**
	 * @
	 * ThTD
	 * @param table
	 * @param target
     */
	static
	hideTh(table, target) {
		Base.setAreVisible(jTool(target), false);
	}

	/**
	 * @
	 *  .xls
	 * @param table
	 * @param fileName 
	 * @param onlyChecked 
	 * @returns {boolean}
     */
	static
	exportGridToXls(table, fileName, onlyChecked) {
		return Export.__exportGridToXls(jTool(table), fileName, onlyChecked);
	}

	/**
	 * @
	 * 
	 * @param table
	 * @param query:  [Object]
	 * @param callback: 
	 * @param isGotoFirstPage: [Boolean default=true]
	 * :
	 * - querykey, query.
	 * - setQuery() 
	 * - query, pagingAfter(query)
	 * - setQueryquery, setQueryquery
	 */
	static
	setQuery(table, query, isGotoFirstPage, callback) {
		const $table = jTool(table);
		const settings = Cache.getSettings($table);
		if (typeof (isGotoFirstPage) !== 'boolean') {
			callback = isGotoFirstPage;
			isGotoFirstPage = true;
		}
		jTool.extend(settings, {query: query});
		if (isGotoFirstPage) {
			settings.pageData.cPage = 1;
		}
		Cache.setSettings($table, settings);
		Core.__refreshGrid($table, callback);
	}

	/**
	 * @
	 * ajaxData
	 * @param table
	 * @param ajaxData: 
	 */
	static
	setAjaxData(table, ajaxData) {
		const $table = jTool(table);
		const settings = Cache.getSettings($table);
		jTool.extend(settings, {ajax_data: ajaxData});
		Cache.setSettings($table, settings);
		Core.__refreshGrid($table);
	}

	/**
	 * @
	 *  
	 * @param table
	 * @param isGotoFirstPage:  [boolean, false]
	 * @param callback: 
	 */
	static
	refreshGrid(table, isGotoFirstPage, callback) {
		const $table = jTool(table);
		const settings = Cache.getSettings($table);
		if (typeof (isGotoFirstPage) !== 'boolean') {
			callback = isGotoFirstPage;
			isGotoFirstPage = false;
		}
		if (isGotoFirstPage) {
			settings.pageData['cPage'] = 1;
			Cache.setSettings($table, settings);
		}
		Core.__refreshGrid($table, callback);
	};

	/**
	 * @
	 * 
	 * @param table
	 * @returns {NodeList} 
     */
	static
	getCheckedTr(table) {
		return table.querySelectorAll('tbody tr[checked="true"]');
	};

	/**
	 * @
	 * 
	 * @param table
	 * @returns {{}}
     */
	static
	getCheckedData(table) {
		const $table = jTool(table);
		return Cache.__getRowData($table, this.getCheckedTr(table));
	};

	/**
	 * []
	 * @param table
	 * @param arg: 
	 * @param callback: 
	 * @returns {*}
	 */
	init(table, arg, callback) {
		const $table = jTool(table);
		// : 
		if (!arg || jTool.isEmptyObject(arg)) {
			Base.outLog('init()', 'error');
			return;
		}

		// : columnData
		if (!arg.columnData || arg.columnData.length === 0) {
			Base.outLog('columnData', 'error');
			return;
		}

		//  gridManagerName: table DOM  grid-manager
		if (typeof arg.gridManagerName !== 'string' || arg.gridManagerName.trim() === '') {
			// gridManagerName
			arg.gridManagerName = Base.getKey($table);
		//  gridManagerName: table DOM  grid-manager
		} else {
			$table.attr('grid-manager', arg.gridManagerName);
		}

		//  
		Cache.cleanTableCacheForVersion();

		// : , 
		const settings = Cache.initSettings($table, arg);

		// : gridManagerName
		if (settings.gridManagerName.trim() === '') {
			Base.outLog('html[grid-manager]gridManagerName', 'error');
			return;
		}

		// : 
		if ($table.hasClass('GridManager-ready') || $table.hasClass('GridManager-loading')) {
			Base.outLog(',', 'error');
			return;
		}

		// 
		$table.addClass('GridManager-loading');

		// 
		this.initTable($table, settings);

		// 
		if (typeof $table.attr('grid-manager-cache-error') !== 'undefined') {
			window.setTimeout(() => {
				Cache.saveUserMemory($table);
				$table.removeAttr('grid-manager-cache-error');
			}, 1000);
		}
		// 
		typeof (callback) === 'function' ? callback(settings.query) : '';
	}

	/**
	 * 
	 * @param $table
	 * @param settings
     */
	initTable($table, settings) {

		// HTMLDOM
		Core.createDOM($table);

		// , dom
		settings.supportAdjust ? Adjust.resetAdjust($table) : '';

		// 
		if (settings.supportAdjust) {
			Adjust.bindAdjustEvent($table);
		}

		// 
		if (settings.supportDrag) {
			Drag.bindDragEvent($table);
		}

		// 
		if (settings.supportSorting) {
			Sort.bindSortingEvent($table);
		}

		// 
		if (settings.supportRemind) {
			Remind.bindRemindEvent($table);
		}

		// 
		if (settings.supportConfig) {
			Config.bindConfigEvent($table);
		}

		// $tablehover
		Hover.onTbodyHover($table);

		// 
		Scroll.bindScrollFunction($table);

		// 
		Menu.bindRightMenuEvent($table);

		// tbodyDOM
		Core.__refreshGrid($table);
	}
}



